"use client";

import { useState } from "react";

export default function Home() {
  const [activeTab, setActiveTab] = useState("overview");

  const tabs = [
    { id: "overview", label: "Overview" },
    { id: "reports", label: "Reports" },
    { id: "analytics", label: "Analytics" },
    { id: "settings", label: "Settings" }
  ];

  const stats = [
    { label: "Total Reports", value: "1,247", change: "+12%" },
    { label: "Active Users", value: "89", change: "+5%" },
    { label: "Completion Rate", value: "94%", change: "+2%" },
    { label: "Avg Response Time", value: "2.3h", change: "-8%" }
  ];

  const recentReports = [
    { id: 1, title: "Monthly Performance Review", status: "Completed", date: "2024-01-15" },
    { id: 2, title: "Quarterly Analysis Report", status: "In Progress", date: "2024-01-14" },
    { id: 3, title: "User Feedback Summary", status: "Pending", date: "2024-01-13" },
    { id: 4, title: "System Health Check", status: "Completed", date: "2024-01-12" }
  ];

  return (
    <div className="min-h-screen bg-white">
      <header className="border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <div className="flex items-center">
              <h1 className="text-2xl font-bold text-black">Dashboard</h1>
            </div>
            <nav className="flex space-x-8">
              {tabs.map((tab) => (
                <button
                  key={tab.id}
                  onClick={() => setActiveTab(tab.id)}
                  className={`text-sm font-medium transition-colors ${
                    activeTab === tab.id
                      ? "text-black border-b-2 border-black"
                      : "text-gray-600 hover:text-black"
                  } pb-4`}
                >
                  {tab.label}
                </button>
              ))}
            </nav>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {activeTab === "overview" && (
          <div>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
              {stats.map((stat, index) => (
                <div key={index} className="bg-gray-50 p-6 rounded-lg">
                  <p className="text-sm font-medium text-gray-600">{stat.label}</p>
                  <p className="text-3xl font-bold text-black mt-2">{stat.value}</p>
                  <p className="text-sm text-gray-500 mt-1">{stat.change}</p>
                </div>
              ))}
            </div>

            <div className="bg-white border border-gray-200 rounded-lg">
              <div className="px-6 py-4 border-b border-gray-200">
                <h2 className="text-lg font-semibold text-black">Recent Reports</h2>
              </div>
              <div className="divide-y divide-gray-200">
                {recentReports.map((report) => (
                  <div key={report.id} className="px-6 py-4 flex justify-between items-center">
                    <div>
                      <p className="font-medium text-black">{report.title}</p>
                      <p className="text-sm text-gray-600">{report.date}</p>
                    </div>
                    <span className={`text-sm font-medium px-3 py-1 rounded-full ${
                      report.status === "Completed" 
                        ? "bg-gray-100 text-gray-800"
                        : report.status === "In Progress"
                        ? "bg-gray-200 text-gray-800"
                        : "bg-gray-50 text-gray-600"
                    }`}>
                      {report.status}
                    </span>
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}

        {activeTab === "reports" && (
          <div>
            <h2 className="text-2xl font-bold text-black mb-6">Reports</h2>
            <div className="bg-gray-50 p-8 rounded-lg text-center">
              <p className="text-gray-600">Reports section coming soon</p>
            </div>
          </div>
        )}

        {activeTab === "analytics" && (
          <div>
            <h2 className="text-2xl font-bold text-black mb-6">Analytics</h2>
            <div className="bg-gray-50 p-8 rounded-lg text-center">
              <p className="text-gray-600">Analytics section coming soon</p>
            </div>
          </div>
        )}

        {activeTab === "settings" && (
          <div>
            <h2 className="text-2xl font-bold text-black mb-6">Settings</h2>
            <div className="bg-gray-50 p-8 rounded-lg text-center">
              <p className="text-gray-600">Settings section coming soon</p>
            </div>
          </div>
        )}
      </main>
    </div>
  );
}
